import useInquiry from "../modules/inquiry/hooks";
import React, { useEffect, useState } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Modal,
  ModalHeader,
  ModalBody
} from "shards-react";

import PageTitle from "../components/common/PageTitle";

import Pagination from "../components/inquiry/pagination";

const Tables = () => {
  const [modalOpen, setModalOpen] = useState();
  const [modalData, setModalData] = useState({});
  const { fetchAllInquiry, inquiry, totalLength, pageNumber } = useInquiry();
  useEffect(() => {
    if (totalLength !== 0) return;
    fetchAllInquiry();
    
  }, []);

  const onChange = item => () => {
    setModalData(item);
    setModalOpen(prev => !prev);
  };

  return (
    <Container fluid className="main-content-container px-4">
      {/* Page Header */}
      <Row noGutters className="page-header py-4">
        <PageTitle
          sm="4"
          title=" "
          
          className="text-sm-left"
        />
      </Row>

      {/* Default Light Table */}
      <Row>
        <Col>
          <Card
            small
            className="mb-4"
            style={{ position: "relative", height: "650px" }}
          >
            <CardHeader className="border-bottom">
              <h6 className="m-0"> </h6>
            </CardHeader>
            <CardBody className="p-0 pb-5">
              <table className="table mb-0" style={{tableLayout:'fixed'}}>
                <thead className="bg-light">
                  <tr>
                    <th scope="col" className="border-0" width="6%" style={{paddingLeft:"30px"}}>
                      #
                    </th>
                    <th scope="col" className="border-0" width="6%">
                      
                    </th>
                    <th scope="col" className="border-0" width="11%">
                      ()
                    </th>
                    <th scope="col" className="border-0" width="16%">
                      
                    </th>
                    <th scope="col" className="border-0" width="11%">
                      
                    </th>
                    <th scope="col" className="border-0" width="35%" >
                      
                    </th>
                    <th scope="col" className="border-0" width="16%">
                      
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {inquiry.map((item, index) => (
                    <tr
                      style={{ cursor: "pointer" }}
                      onClick={onChange(item)}
                      key={item.id}
                    >
                      
                      <td style={{paddingLeft:"30px"}}>{totalLength -  (pageNumber - 1) * 10 - index}</td>
                      <td style={{whiteSpace:"nowrap", textOverflow:"ellipsis", overflow:"hidden"}}>{item.name}</td>
                      <td style={{whiteSpace:"nowrap", textOverflow:"ellipsis", overflow:"hidden"}}>{item.company}</td>
                      <td style={{whiteSpace:"nowrap", textOverflow:"ellipsis", overflow:"hidden"}}>{item.email}</td>
                      <td>{item.contact}</td>
                      <td style={{whiteSpace:"nowrap", textOverflow:"ellipsis", overflow:"hidden"}}>
                        {item.message}
                      </td>
                      <td>{item.regdate}</td>
                    </tr>
                  ))}
                </tbody>
                <Modal open={modalOpen} centered={true}>
                  <ModalHeader className="d-block">
                    
                    <div
                      onClick={onChange({})}
                      style={{
                        cursor: "pointer",
                        position: "absolute",
                        right: "1rem",
                        top: "1rem"
                      }}
                    >
                      <i className="material-icons">close</i>
                    </div>
                  </ModalHeader>
                  <ModalBody>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted"></strong>
                      </Col>
                      <p>{modalData.name}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted"></strong>
                      </Col>
                      <p>{modalData.company}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted"></strong>
                      </Col>
                      <p>{modalData.email}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted"></strong>
                      </Col>
                      <p>{modalData.contact}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted"></strong>
                      </Col>
                      <Col md="9" style={{ paddingLeft: 0 }}>
                        <p>{modalData.message}</p>
                      </Col>
                    </Row>
                  </ModalBody>
                </Modal>
              </table>
            </CardBody>
            <Pagination
              style={{ 
                display:"flex",
                position: "absolute", bottom: 10,  
                justifyContent:'center'
              }}
            />
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Tables;
