import api from "../api/api"

const SearchButton = React.memo(() => {
  const [search, setSearch] = useState("");
  const test = useRef(null)

  const searchBox = useCallback(e => {
    const {
      target: { value }
    } = e;
    setSearch(value);
  }, [search]);

  const onSubmit = useCallback(e => {
    e.preventDefault();

  }, [search])

  return (
    <Form
      className="main-navbar__search d-none d-md-flex d-lg-flex"
      style={{ flexBasis: "250px" }}
      onSubmit={onSubmit}
    >
      <InputGroup seamless className="ml-3">
        <InputGroupAddon type="prepend">
          <InputGroupText>
            <i className="material-icons">search</i>
          </InputGroupText>
        </InputGroupAddon>
        <FormInput
          className="navbar-search"
          placeholder="Search for something..."
          value={search}
          onChange={searchBox}
          ref={test}
        />
      </InputGroup>
    </Form>
  )
})

const MediaTable = React.memo(({ history }) => {
  const [products, setProducts] = useState([]);

  useEffect(async () => {
    console.log(await api.getProducts())
    setProducts(await api.getProducts());
    // fetchAllMedias();
  }, []);
  return (
    <table className="table mb-0">
      <thead className="bg-light">
        <tr>
          <th width="6.50618%" scope="col" className="border-0" style={{ paddingLeft: "30px" }}>
            #
          </th>
          <th width="46.66881%" scope="col" className="border-0">
            
          </th>
          <th width="15.1594%" scope="col" className="border-0">
            
          </th>
          <th width="12.8002%" scope="col" className="border-0">
            
          </th>
          <th width="18.8002%" scope="col" className="border-0">
            
          </th>
        </tr>
      </thead>
      <tbody>
        {products.map((item, index) => (
          <tr
            onClick={e => {
              if (e.target.id !== "close") {
                history.push("/write", item.id);
              }
            }}
            style={{ cursor: "pointer", position: "relative" }}
            key={item.id}
          >
            <td style={{ paddingLeft: "30px" }}>{item.id}</td>
            {/* <td>{item.title}</td> */}
            <td>
              {item.title.slice(0, 40) +
                (item.title.length > 40 ? "..." : "")}
            </td>
            <td>{item.price}</td>
            <td>{

              item.count
            }</td>
            <td>{
              // typeof(item.regdate)=== "string" ? item.regdate.split(' ')[0]: "null"
              item.createdAt
            }</td>
          </tr>
        ))}
      </tbody>
    </table>
  )
})

