import React, { useEffect, useState } from "react";
import { Container, Row, Col, Button, Form } from "shards-react";

import PageTitle from "../components/common/PageTitle";
import Editor from "../components/add-new-post/Editor";
import SidebarActions from "../components/add-new-post/SidebarActions";
import SidebarCategories from "../components/add-new-post/SidebarCategories";
import useMedia from "../modules/media/hooks";
import { BASE_URL } from "../modules/host";
import { post } from "axios";
import api from "../api/api";

const AddNewPost = ({ location, history }) => {
  const a = require("../images/sampleImage.png");
  const [productInfo, setProductInfo] = useState({ categoryId: 1 })
  const [image, setImage] = useState(a);

  const [loading, setLoading] = useState(false);
  console.log(location.state)

  useEffect(async () => {

    if (location.state) {
      setLoading(true)
      const data = await api.getOneProduct(location.state)
      setLoading(false);
      setProductInfo(data);
    }
  }, []);

  const onSubmit = async e => {
    e.preventDefault();
    if (productInfo.id) {
      await api.patchProduct(productInfo.id, productInfo)
    } else {

      await api.addProduct(productInfo)
    }
  };

  if (loading) {
    return (<Container fluid className="main-content-container px-4 pb-4" style={{
      display: "flex",
      alignItems: 'center',
      justifyContent: 'center'
    }}>
      .
      <i className="material-icons" style={{ fontSize: 50, marginLeft: 10 }}>cached</i>
    </Container>)
  }

  return (

    <Container fluid className="main-content-container px-4 pb-4">
      <Form
        onSubmit={e => {
          e.preventDefault();
          console.log(e);
        }}
        action={`${BASE_URL}news/add`}
        method="post"
      >
        {/* Page Header */}
        <Row noGutters className="page-header py-4">
          <PageTitle
            sm="4"
            title=""

            className="text-sm-left"
          />
        </Row>
        <Row>
          <Col className="mb-3" style={{ display: 'flex', justifyContent: 'flex-end' }}>
            <Button
              size="lg"
              theme="primary"
              className="col-md-1 mr-3"
              type="submit"

              onClick={onSubmit}
            >
              
            </Button>
            {productInfo.id > 0 && <Button
              size="lg"
              id="close"
              theme="danger"
              className="col-md-1"
              onClick={() => {
                window.confirm(" ?") && api.deleteProduct(productInfo.id).then(
                  history.push("/products"),
                );
              }}
            >
              
            </Button>}
          </Col>
        </Row>

        <Row>
          {/* Editor */}
          <Col lg="12" md="12">
            <Editor
              value={productInfo}
              setValue={setProductInfo}
            />
          </Col>

          {/* Sidebar Widgets */}
          <Col lg="3" md="12">
            {/* <SidebarActions
              image={image}
              setImage={setImage}
              imageLoading={imageLoading}
              setImageLoading={setImageLoading}
            /> */}
            {/* <SidebarCategories
              press={press}
              setPress={setPress}
              date={date}
              setDate={setDate}
              regDate={regDate}
              setRegDate={setRegDate}
            /> */}


          </Col>
        </Row>
      </Form>
    </Container>
  );
};

// const AddNewPost = () => (
// );

export default AddNewPost;
