const Editor = props => {

  const { value, setValue } = props;
  const [rawHtml, setRawHtml] = useState('');
  const [showRaw, setShowRaw] = useState(false);
  const [category, setCategory] = useState(0);

  const quill = useRef();

  useEffect(() => {
    console.log(value)
    setRawHtml(value.detail)
  }, [value])


  const handleChangeRaw = (html) => {
    setRawHtml(html);
    setValue({ ...value, detail: html })


  }
  const handleClickShowRaw = () => {
    if (showRaw === true) {
      setValue({ ...value, detail: rawHtml })
    }
    const isEditingRaw = showRaw;


    setShowRaw(!isEditingRaw)
    syncViews(isEditingRaw)
  }

  const syncViews = (fromRaw) => {
    if (fromRaw) setValue({ ...value, detail: rawHtml })
    else setRawHtml(value.detail)
  }

  const modules = {
    // font,
    ImageResize: {
      displayStyles: {
        backgroundColor: 'black',
        border: 'none',
        color: 'white'
      },
      modules: ['Resize', 'DisplaySize', 'Toolbar'],
    },
    toolbar: {
      container: '#toolbar',
      handlers: {
      }
    }
  };

  const formats = [
    'header',
    'bold', 'italic', 'underline', 'strike',
    'blockquote', 'code-block',
    'list', 'bullet',
    'script',
    'indent',
    'link', 'image',
    'size',
    'font',
    'color', 'background',
    'code',
    'display',
    'width',
    'height',
    'float',
    'align',
    'family'
  ]

  const onChange = e => {
    const {
      target: { value: nValue, name }
    } = e;

    const newValue = { ...value };
    newValue[name] = nValue;

    setValue(newValue)
  };
  const changeCategory = (e) => {

    const { target: { value: nValue } } = e;

    setCategory(nValue)

  }

  return (
    <Card small className="mb-3">
      <CardBody>
        {/* <Form className="add-new-post"> */}

        <Row>
          <Col md="12" className="form-group">
            <label htmlFor="title"></label>
            <FormInput
              id="title"
              size="lg"
              name="title"
              placeholder="Your Post Title"
              value={value.title}
              onChange={onChange}

            />
          </Col>
        </Row>
        <Row>
          <Col md="6" className="form-group">
            <label htmlFor="price"></label>
            <FormInput
              id="price"
              name="price"
              type="text"
              placeholder=" ."
              value={value.price}
              onChange={onChange}
            />
          </Col>
          <Col md="6" className="form-group">
            <label htmlFor="productCode"></label>
            <FormInput
              id="productCode"
              name="productCode"
              type="text"
              placeholder=" ."
              value={value.productCode}
              onChange={onChange}
            />
          </Col>

        </Row>
        <Row>
          <Col md="6" className="form-group">
            <label htmlFor="sojae"></label>
            <FormInput
              id="sojae"
              type="text"
              placeholder="  ."
              name="sojae"
              value={value.sojae}
              onChange={onChange}
            />
          </Col>
          <Col md="6" className="form-group" >
            <label htmlFor="fit"></label>
            <FormInput
              id="fit"
              type="text"
              placeholder="  "
              name="fit"
              value={value.fit}
              onChange={onChange}
            />

          </Col>
        </Row>
        <Row>
          <Col md="6" className="form-group">
            <label htmlFor="setak"></label>
            <FormInput
              id="setak"
              type="text"
              name="setak"
              placeholder=" ."
              value={value.setak}
              onChange={onChange}
            />
          </Col>
          <Col md="6" className="form-group" >
            <label htmlFor="model"></label>
            <FormInput
              id="model"
              name="model"
              type="text"
              placeholder="  ."
              value={value.model}
              onChange={onChange}
            />

          </Col>
        </Row>
        <Row>
          <Col md="6" className="form-group" >
            <label htmlFor="mainCategory"> </label>
            <FormSelect
              id="mainCategory"
              name="mainCategory"
              onChange={changeCategory}
            >
              {
                constants.mainCategory.map(item => (
                  <option value={item.id}>{item.name}</option>
                ))
              }
            </FormSelect>

          </Col>
          <Col md="6" className="form-group" >
            <label htmlFor="categoryId"> </label>
            <FormSelect
              id="categoryId"
              name="categoryId"
              onChange={onChange}
            >
              {
                constants.mainCategory[category].subCategory.map(item => (
                  <option value={item.id}>{item.name}</option>
                ))
              }

            </FormSelect>

          </Col>

        </Row>

        <CustomToolbar onClickRaw={handleClickShowRaw} />
        <ReactQuill
          id='editor'
          theme="snow"
          ref={quill}
          modules={modules}
          className="add-new-post__editor mb-1"
          placeholder="   ."
          onChange={handleChangeRaw}
          value={rawHtml || ""}
          name="detail"
          formats={formats}

        >

        </ReactQuill>

        <Modal size="lg" open={showRaw} centered={true}>
          <ModalHeader className="d-block">
            test
            {/* {value.title.slice(0, 40) + (value.title.length > 40 ? "..." : "")} */}
            <div
              onClick={() => {
                handleClickShowRaw()
              }}
              style={{
                cursor: "pointer",
                position: "absolute",
                right: "1rem",
                top: "1rem"
              }}
            >
              <i className="material-icons">close</i>
            </div>
          </ModalHeader>
          <ModalBody>
            <InputGroup className="mb-3">
              <FormTextarea style={{ height: "70vh" }} size="lg" onChange={(e) => (handleChangeRaw(e.target.value))} value={rawHtml} />
            </InputGroup>
            <Button style={{ marginLeft: "calc(100% - 1.875rem - 28px)" }} onClick={handleClickShowRaw}></Button>
          </ModalBody>
        </Modal>
        {/* </Form> */}
      </CardBody>
    </Card>
  );
};

export default Editor;
